#productfilters {
  justify-content: center;
  align-items: center;
  display: flex;
}

#categoryform, #searchform {
  color: #fff;
  background-color: #000;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

#searchform > label {
  font-weight: 700;
}

#categoryform input, #categoryform select, #searchform input, #searchform select {
  border: 0;
  outline: 0;
  min-height: 2.2rem;
  margin: 0;
  padding: .5rem .75rem;
}

#searchform input {
  border-radius: 15px 0 0 15px;
}

#categoryform button, #searchform button {
  cursor: pointer;
  background-color: orange;
  border: 0;
  border-radius: 0 15px 15px 0;
  outline: 0;
  justify-content: center;
  align-items: center;
  min-height: 2.2rem;
  margin: 0;
  padding: .4rem .75rem;
  font-size: .9rem;
  font-weight: 300;
  display: flex;
}

#searchform button:focus, #searchform button:not(:disabled):hover {
  background-color: #ff0;
  outline: 0;
  box-shadow: 0 0 15px #ff0, 0 0 30px orange;
}

#search-category {
  border-radius: 15px 0 0 15px;
}

#search-category, #search-category option {
  text-transform: capitalize;
}
